.main1 {
    margin: 2rem;
    margin-top: 10.3vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.container1 {
    width: 1179px;
    height: 479px;
    border: 0.01px solid gray;
    border-radius: 23% 21% 12% 19% / 16% 58% 43% 76%;
    background: rgb(0, 102, 147);
    background: linear-gradient(180deg, rgba(0, 102, 147, 1) 19%, rgba(0, 61, 88, 1) 100%);
}

.register1 {
    font-size: 2.5rem;
    font-family: "Protest Guerrilla", sans-serif;
    color: rgb(255, 255, 255);
    text-align: center;
}

.content1 {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding-left: 9.5rem;
    height: 30rem;
    justify-content: center;
    margin-top: -39.9px;
}

.input-box1 {
    margin: 19px;
    width: 302px;
    color: white;
}

.input-box1 label {
    font-size: 20px;
    color: white;
}

.input-box1 input {
    height: 32px;
    width: 90%;
    border: none;
    background-color: transparent;
    color: white;
    border-bottom: 2px solid rgb(255, 255, 255);
    outline: none
}

.Submit1 {
    width: 13.4em;
    height: 3rem;
    border-radius: 83.0px;
    font-size: 1.4rem;
    border: none;
    background-color: bisque;
    letter-spacing: 2px;
    cursor: pointer;
    margin-top: 2rem;
}