@import url('https://fonts.googleapis.com/css2?family=Protest+Guerrilla&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

* {
    padding: 0;
    margin: 0;

}

.body {
    display: flex;

}

.header {
    background-color: #138397;
    width: 100vw;
    height: 12vh;
    display: inline-flex;
    justify-content: flex-end;
    align-items: center;
}

.links {
    width: auto;
}

.item {
    margin: 1vw;
    font-size: 1.4rem;
    color: white;
    text-decoration: none;
    font-family: "Protest Guerrilla", sans-serif;
    font-weight: 350;
    font-style: normal;
    transition: all 0.3s ease 0s;
}

.item:hover {
    color: rgb(121, 255, 121);
}

.button1 {
    border: 4px solid #0e8145;
    background-color: #17E379;
    padding: 0.5rem;
    border-radius: 9px;
    cursor: pointer;
    width: 9rem;
}

.titleCont {
    width: 55vw;
}

.titleCont1 {
    width: -15.6vw;
}

.title {
    font-size: 2rem;
    font-family: "Pacifico", cursive;
    font-weight: 400;
    font-style: normal;
    color: white;
    letter-spacing: 4px;
}


@media only screen and (max-width: 1240px) {
    .item {
        font-size: 1rem
    }

    .title {
        font-size: 1.5rem;
    }
}

@media only screen and (max-width: 884px) {
    .item {
        font-size: 0.9rem
    }

    .title {
        font-size: 1.4rem;
    }
}