.main2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 9rem;
}

.card {
    margin: 8px;
    margin-top: 16px;
    border: 2px solid rgb(146, 146, 146);
    width: 17.7rem;
    height: 26.9rem;
    border-radius: 12px;
    border-radius: 10px;
}

.img {
    width: 100%;
    height: 48%;
    border-bottom: 3px solid rgb(169, 169, 169);
    text-align: center;
}

.img img {
    width: 97%;
    border-radius: 10px;
    -webkit-box-shadow: 0px 36px 42px -19px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 36px 42px -19px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 36px 42px -19px rgba(0, 0, 0, 0.75);
}

.text {
    padding: 10px;
}

.elements {
    margin: 5px;
    font-size: 1.1rem;
}